@import "../../../scss/colors.scss";
@import "../../../scss/repeat.scss";

.categoriesList {
  width: 100%;
  max-height: 92%;
  @extend %scrollList;
  padding: 0 32px;

  overflow-x: hidden;

  ul {
    height: calc(100% - 72px);
    max-height: calc(100% - 72px);
    margin-bottom: 16px;
  }

  &__item {
    width: 100%;

    svg,
    svg path {
      transition: all 0.2s ease;
    }

    &[class~="more"] > &Top {
      svg {
        transform: rotate(180deg);
      }
    }

    &Top {
      cursor: pointer;
      padding: 24px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      button {
        @extend %button;
      }

      &:hover {
        button {
          svg path {
            fill: $btnHover;
          }
        }
      }

      &:active {
        button {
          svg path {
            fill: $btnActive;
          }
        }
      }
    }

    &Left {
      display: flex;
      align-items: center;
    }

    &Image {
      width: 48px;
      height: 48px;
      margin-right: 14px;
      background-color: rgba($primary, 0.6);
      border-radius: 12px;
      overflow: hidden;
    }

    &Info {
      h2 {
        @extend %body1;
        font-weight: 600;
        margin-bottom: 5px;
      }
      span {
        @extend %subHead4;
        color: #808191;
      }
    }
  }

  &__itemMoreInfo {
    display: flex;
    justify-content: space-between;

    [class~="button"] {
      width: 91px !important;
    }

    ul {
      display: flex;
      align-items: row;
      flex-wrap: wrap;
      margin-left: -12px;
      margin-right: 12px;
      margin-bottom: -8px;
    }
  }

  &__subcategory {
    @extend %body2;
    color: #808191;
    border-radius: 10px;
    border: 0.5px solid rgba(0, 0, 0, 0.12);
    background: #f2f3f5;
    width: fit-content;
    padding: 10px 12px;
    margin-left: 12px;
    margin-bottom: 8px;
  }

  &__loader {
    width: fit-content;
    margin: 0 auto;
  }

  [class~="button"] {
    width: 200px;
    @extend %btnSecondary;
    margin: 0 auto;
  }
}
