@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.PROs {
  width: 100%;
  padding: 32px 0;
  height: 84%;
  max-height: 75vh;
  background-color: $white;
  border-radius: 24px;
  box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

  &__header {
    @extend %alignedRow;
    padding: 0 32px;
    justify-content: space-between;
  }

  &__nav {
    @extend %alignedRow;
    margin-right: 12px;
    &Item {
      @extend %smallBtnSecondary;

      &:nth-child(1) {
        margin-right: 12px;
        width: 93px;
      }

      &:nth-child(2) {
        width: 162px;
      }

      &[class~="active"] {
        @extend %smallBtnPrimary;
      }
    }
  }

  [class~="searchInput"] {
    margin: 0 16px;
  }

  &__filters {
    @extend %alignedRow;
    width: 67%;
    max-width: 662px;
  }

  &__filters > button {
    @extend %button;
    border-radius: 16px;
    background-color: rgba(228, 228, 228, 0.2);
    color: #808191;
    height: 56px;
    padding: 0 16px;
    outline-color: $btnHover;

    svg {
      margin-right: 8px;
    }

    &:hover {
      outline: 1px solid $btnHover !important;
      background-color: $white;
      color: $btnHover;

      svg path,
      svg circle {
        stroke: $btnHover;
      }
    }

    &:active {
      outline: 1px solid $btnActive !important;
      color: $btnActive;

      svg path,
      svg circle {
        stroke: $btnActive;
      }
    }
  }

  &__infoBar {
    @extend %subHead4;
    color: #b2b3bd;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 32px;
    border-bottom: 1px solid #e4e4e4;
    padding: 0 32px 12px 68px;

    p:nth-child(1) {
      width: 87px;
    }

    p:nth-child(2) {
      width: 13%;
    }

    p:nth-child(3) {
      width: 17.7%;
    }

    p:nth-child(4) {
      width: fit-content;
    }

    p[class~="hide"]:nth-child(4) {
      display: none;
    }

    p:nth-child(5) {
      width: 17.7%;
    }

    p:nth-child(6),
    p:nth-child(7) {
      width: 7.8%;
    }

    p:nth-child(8) {
      width: 8.8%;
    }
  }
}
