@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.PROsCard {
  @extend %body2;
  color: $textBlack;
  width: 100%;

  &__top {
    @extend %alignedRow;
    justify-content: space-between;
    padding-left: 36px;
    position: relative;
    width: 100%;
    min-height: 87px;
  }

  &-active &__topBlock:nth-child(1) > svg {
    transform: rotate(180deg);
  }

  &__topBlock:nth-child(1) {
    position: absolute;
    left: 0;
    @extend %button;
    width: fit-content;

    svg path,
    svg {
      transition: all 0.2s ease;
    }

    &:hover {
      svg path {
        fill: $btnHover;
      }
    }

    &:active {
      svg path {
        fill: $btnActive;
      }
    }
  }

  &__topBlock:nth-child(2) {
    width: 87px;
    height: 87px;
    background-color: rgba(228, 228, 228, 0.2);
    position: relative;
    border-radius: 8px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 8px;
    }
  }

  &__topBlock {
    word-wrap: break-word;
  }

  &__topBlock:nth-child(3) {
    width: 13%;
  }

  &__topBlock:nth-child(4) {
    width: 17.7%;
    color: #808191;
  }

  &__topBlock:nth-child(5) {
    width: fit-content;
    color: #ff754c;
    text-decoration: underline;
    cursor: pointer;

    &.hide {
      display: none;
    }
  }

  &__topBlock:nth-child(6) {
    width: 17.7%;
  }

  &__topBlock:nth-child(7),
  &__topBlock:nth-child(8) {
    width: 7.8%;
  }

  &__topBlock:nth-child(9) {
    width: 8.8%;
    text-align: center;
    color: #ff754c;
    cursor: pointer;

    span {
      text-decoration: underline;
    }
  }

  &__bottom {
    margin-top: 12px;
    width: 100%;
    padding-left: 36px;
    width: 100%;
  }

  &__contacts {
    margin-bottom: 24px;

    p:nth-child(1) {
      margin-bottom: 8px;
    }

    p {
      @extend %subText;
      color: $textBlack;
      word-wrap: break-word;
      max-width: 100%;

      span {
        @extend %subHead3;
        margin-right: 14px;
      }
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__about {
    width: 46%;
    max-width: 403px;

    span {
      display: block;
      margin-bottom: 16px;
    }

    p {
      padding: 16px 24px;
      min-height: 164px;
      background-color: #f7f7f7;
      border-radius: 8px;
    }
  }

  &__categories {
    width: 40%;

    span {
      display: block;
      margin-bottom: 16px;
    }

    &Item:nth-of-type(1) {
      margin-bottom: 16px;
    }

    &Item {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -12px -12px 0;

      div {
        border: 0.5px solid rgba(0, 0, 0, 0.12);
        border-radius: 10px;
        background-color: #f2f3f5;
        width: fit-content;
        padding: 12px;
        color: #808191;
        margin: 0 12px 12px 0;
      }
    }
  }

  &__footer {
    display: flex;
    align-items: flex-end;
    width: 100%;
    margin-top: 24px;
  }

  &__footerСontrols {
    display: flex;
    justify-content: flex-end;
    max-width: 320px;
    width: inherit;
    margin-left: auto;

    [class~="button"] {
      @extend %btnSecondary;
      max-width: 156px;

      &:not(:last-child) {
        margin-right: 8px;
      }
    }
  }

  &__certificates {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -24px -24px 0;
    max-width: 403px;
    width: 46%;

    a {
      margin: 0 24px 24px 0;
      display: block;
      position: relative;
      min-width: 60px;
      min-height: 60px;
      width: 60px;
      height: 60px;
      background-color: #c4c4c4;
      border-radius: 8px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
      }
    }
  }

  &__deleteBtn {
    transition: all 0.2s ease;
    transform: scale(0);
    @extend %button;
    position: absolute;
    top: -10px;
    right: -10px;
    width: 24px;
    height: 24px;
    background-color: $white;
    box-shadow: 0px 6px 12px -3px rgba(0, 0, 0, 1);
    border-radius: 50%;

    svg {
      width: 16px;
      height: 16px;
    }

    &:hover {
      svg path {
        transition: all 0.2s ease;
        fill: $error;
      }
    }

    &.showDelete {
      transform: scale(1);
    }
  }
}
