@import "../../scss/repeat.scss";
@import "../../scss/colors.scss";

.selectInput {
  position: relative;
  width: inherit;

  &__btn {
    @extend %button;
    justify-content: space-between;
    width: 100%;
    height: 56px;
    padding: 18px 24px;
    background-color: rgba(228, 228, 228, 0.2);
    border-radius: 16px;

    svg {
      transition: all 0.2s ease;
    }

    &:hover {
      outline: 1px solid $btnHover !important;
      background-color: #fff;
    }
  }

  &__dropdown {
    transform: translateY(-100px) scale(0);
    transition: transform 0.2s ease;
    min-width: 100%;
    min-height: 100px;
    border-radius: 24px;
    background-color: $white;
    box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);
    position: absolute;
    right: 0;
    top: 72px;
    padding: 32px;
    z-index: 10;

    h3 {
      @extend %heading5;
      color: #808191;
      margin-bottom: 16px;
      text-align: center;
    }

    ul {
      @extend %alignedRow;
      margin-right: -12px;

      li {
        margin-right: 12px;
      }

      button {
        @extend %smallBtnSecondary;
        padding: 0 18px;
        width: fit-content;
      }

      button.selected {
        @extend %smallBtnPrimary;
      }
    }
  }

  &-more > &__btn {
    outline: 1px solid $btnHover !important;
    background-color: #fff;

    svg {
      transform: rotate(180deg);

      path {
        fill: $primary;
      }
    }
  }

  &-more > &__dropdown {
    transform: translateY(0) scale(1);
  }
}
