@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.users {
  width: 100%;
  padding: 32px 0;
  height: 84%;
  max-height: 75vh;
  background-color: $white;
  border-radius: 24px;
  box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

  &__header {
    @extend %alignedRow;
    padding: 0 32px;
    justify-content: space-between;

    [class~="searchInput"] {
      margin: 0 16px;
    }

    [class~="selectInput"] {
      width: 224px;
    }
  }

  &__infoBar {
    @extend %subHead4;
    color: #b2b3bd;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 32px;
    border-bottom: 1px solid #e4e4e4;
    padding: 0 32px 12px 68px;

    p:nth-child(1) {
      width: 14.5%;
    }

    p:nth-child(2) {
      width: 17%;
    }

    p:nth-child(3) {
      width: 10.5%;
    }

    p:nth-child(4) {
      width: 24.3%;
    }

    p:nth-child(5) {
      width: 5.8%;
    }

    p:nth-child(6) {
      width: 8.4%;
    }

    p:nth-child(7) {
      width: 48px;
    }
  }

  &__content {
    width: 100%;
    height: inherit;
    @extend %scrollList;
    justify-content: start;
    padding: 24px 32px 0 32px;

    overflow-x: hidden;

    [class~="usersCard"] {
      margin-bottom: 24px;
    }

    & > [class~="button"] {
      width: 200px;
      @extend %btnSecondary;
      margin: 0 auto;
    }
  }

  &__loader {
    height: 56px;
    width: fit-content;
    margin: 0 auto;
  }
}
