@import "../../../scss/colors.scss";
@import "../../../scss/repeat.scss";

.addAdmin {
  width: 100%;
  max-height: 92%;
  height: 92%;
  @extend %scrollList;
  padding: 0 32px;
  overflow-x: hidden;

  &__loader {
    margin-top: 20px;
    height: 56px;
  }

  [class~="input"] {
    margin-top: 20px;
    max-width: 274px;
  }

  [class~="button"] {
    margin-top: 20px;
    width: 200px;
    @extend %btnSecondary;
  }
}
