@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.input {
  position: relative;
  transition: padding-bottom 0.2s ease;
  padding-bottom: 0px;
  font-weight: 500;
  font-family: Inter;

  &Area {
    outline: none;
    border: none;
    background-color: rgba(228, 228, 228, 0.3);
    position: relative;
    z-index: 1;
    width: 100%;
    height: 56px;
    font-size: 14px;
    padding: 18px 22px;
    line-height: 20px;
    border-radius: 8px;

    &:focus {
      outline: 2px solid $primary !important;
    }

    &::placeholder {
      font-family: Inter;
      font-size: 16px;
      opacity: 0.4;
    }
  }

  textarea {
    resize: none;
    height: 300px;
  }

  span.label {
    display: block;
    color: #b2b3bd;
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 16px;
  }

  span.error {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $error;
  }
  &_error {
    .inputArea {
      color: $error;
      &::placeholder {
        color: $error;
      }
      border: 2px solid $error;
    }
    padding-bottom: 28px;
  }
}
