@import "../../../scss/colors.scss";
@import "../../../scss/repeat.scss";

.allPROs {
  width: 100%;
  height: inherit;
  @extend %scrollList;
  justify-content: start;
  padding: 24px 32px 0 32px;

  overflow-x: hidden;

  &__loader {
    height: 56px;
    width: fit-content;
    margin: 0 auto;
  }

  [class~="PROsCard"] {
    margin-bottom: 24px;
  }

  & > [class~="button"] {
    width: 200px;
    @extend %btnSecondary;
    margin: 0 auto;
  }
}
