@font-face {
  font-family: "Poppins";
  src: url("./fonts/Poppinsregular.woff2") format("woff2"),
    url("./fonts/Poppinsregular.woff") format("woff"),
    url("./fonts/Poppinsregular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
