@import "../../scss/repeat.scss";
@import "../../scss/colors.scss";

.filtersModal {
  [class~="modal__content"] {
    flex-direction: column;
    width: 80%;
    max-width: 554px;
    box-shadow: 0px 8px 20px rgba(118, 118, 118, 0.25);
    border-radius: 24px;
    padding: 24px 0;
  }

  h2 {
    @extend %heading5;
  }

  &__content {
    display: flex;
    width: 100%;
    height: 50vh;
    max-height: 514px;
    padding: 24px 32px;
    margin: 24px 0;
    border-bottom: 1px solid #e4e4e4;
    border-top: 1px solid #e4e4e4;
    justify-content: space-between;

    ul {
      @extend %scrollList;
      padding-right: 10px;
      width: 45%;

      h3 {
        @extend %subHead1;
        text-align: left;
        margin-bottom: 16px;
      }

      li {
        margin-bottom: 16px;

        label {
          @extend %alignedRow, %checkbox, %body1;
          color: #808191;
          text-align: left;
          width: 100%;
          word-wrap: break-word;
        }
      }
    }
  }

  &__footer {
    @extend %alignedRow;
    justify-content: space-between;
    width: 100%;
    padding: 0 32px;

    button:first-child {
      @extend %button;
      width: fit-content;

      &:hover {
        color: $btnHover;
      }

      &:active {
        color: $btnActive;
      }
    }

    [class~="button"] {
      @extend %btnSecondary;
      width: 166px;
    }
  }
}
