@import "../../scss/repeat.scss";
@import "../../scss/colors.scss";

.reviewsModal {
  [class~="modal__content"] {
    width: 90%;
    height: 90%;
    max-height: 820px;
    border-radius: 20px;
    padding: 48px 0;
    overflow: hidden;
  }

  &__header {
    @extend %alignedRow;
    width: 100%;
    padding: 0 32px;

    h2 {
      @extend %heading5;
      color: $textBlack;
      margin-left: 61px;
    }
  }

  &__userBlock {
    @extend %alignedRow;

    div:first-child {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: rgba(228, 228, 228, 0.2);
      overflow: hidden;
      margin-right: 12px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    p {
      @extend %subHead4;
      color: #d1d1d1;
      text-align: left;

      &:first-child {
        @extend %subHead2;
        color: $textBlack;
        margin-bottom: 4px;
      }
    }
  }

  &__ratingBlock {
    @extend %alignedRow;
    width: 322px;
    justify-content: space-between;
    margin-left: auto;
  }

  &__userRating {
    @extend %subHead4;
    text-align: right;
    width: fit-content;

    div {
      @extend %alignedRow, %heading4;
      color: $primary;

      svg {
        margin-right: 10px;
      }
    }
  }

  &__ratingFilters {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 216px;
    margin-bottom: -7px;

    button {
      @extend %button;
      margin-bottom: 7px;
      width: fit-content;
      height: fit-content;
      padding: 4px 8px;
      border-radius: 8px;
      background-color: rgba(0, 28, 61, 0.05);
      transition: background-color 0.2s ease, color 0.2s ease;
      outline-color: $btnHover;

      svg {
        width: 14px;
        height: 14px;
        margin-right: 2px;

        path {
          transition: fill 0.2s ease;
        }
      }

      span {
        margin-left: 6px;
      }

      &.selected {
        background-color: $primary;
        color: $white;

        &:hover {
          outline: none !important;
          background-color: $primary;
        }

        svg path {
          fill: $white;
        }
      }

      &:first-child {
        margin-left: 8px;
      }

      &:active {
        outline: 1px solid $btnActive !important;
        background-color: $white;
      }

      &:hover {
        outline: 1px solid $btnHover !important;
        background-color: $white;
      }
    }
  }

  &__infoBar {
    @extend %subHead4;
    color: #b2b3bd;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 32px;
    margin-top: 32px;
    padding-bottom: 12px;
    border-bottom: 1px solid #e4e4e4;
    text-align: left;

    p:nth-child(1) {
      width: 16.6%;
    }

    p:nth-child(2) {
      width: 60.6%;
    }

    p:nth-child(3) {
      width: 12.9%;
    }
  }

  ul {
    @extend %scrollList;
    height: 74%;
  }

  li {
    @extend %body2;
    color: #5e5c5c;
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
    width: 100%;
    padding: 0 32px;
    text-align: left;

    & > div:nth-child(1) {
      @extend %subHead2;
      color: $textBlack;
      width: 16.6%;
    }

    div:nth-child(2) {
      width: 60.6%;
      div {
        word-break: break-word;
        display: flex;
        width: fit-content;
        margin-bottom: 8px;
      }

      span:first-child {
        @extend %subHead2;
        color: $textBlack;
        margin-right: 12px;
      }

      span:last-child {
        color: #a4a4a4;
      }
    }

    div:nth-child(3) {
      width: 12.9%;
    }
  }

  [class~="button"] {
    @extend %btnSecondary;
    width: 166px;
    margin-right: 32px;
    margin-left: auto;
  }
}
