@import "../../../scss/colors.scss";
@import "../../../scss/repeat.scss";

.addCategory {
  width: 100%;
  max-height: 92%;
  height: 92%;
  @extend %scrollList;
  padding: 0 32px;
  overflow-x: hidden;

  & > &__block {
    margin-bottom: 20px;
  }

  &__uploadFile {
    padding-bottom: 32px;
    border-bottom: 1px solid #e4e4e4;
    margin-bottom: 28px;

    &_error {
      margin-top: 8px;
      font-family: Inter;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: $error;
    }
  }

  &__uploadInput {
    input {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    overflow: hidden;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    @extend %subHead4;
    color: $textBlack;
    border: 2px dashed #e3e6ec;
    border-radius: 16px;
    width: 110px;
    height: 110px;
  }

  & > &__block {
    justify-content: space-between;
  }

  &__block {
    display: flex;
    width: 100%;

    [class~="button"] {
      position: relative;
      margin: 0 !important;
      svg {
        position: absolute;
        left: 16px;
        margin-right: 8px;
      }
    }

    [class~="input"] {
      width: inherit;
      max-width: 274px;
    }
  }

  &__subcategory {
    margin-bottom: 20px;

    button {
      @extend %button;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: $white;
      box-shadow: 0px 24px 43px -12px rgba(34, 60, 80, 0.4);
      margin-left: 16px;

      svg {
        width: 24px;
        height: 24px;
      }

      &:hover {
        svg path {
          transition: all 0.2s ease;
          fill: $error;
        }
      }

      &:last-child:hover {
        svg path {
          transition: all 0.2s ease;
          fill: $primary;
        }
      }
    }
  }

  &__label {
    @extend %subHead3;
    color: #808191;
    display: block;
    margin-bottom: 16px;
  }

  &__subCategoryControls {
    @extend %alignedRow;
    height: 56px;
    justify-content: center;
  }

  &__loader {
    margin: 0 auto;
    width: fit-content;
  }

  [class~="button"] {
    width: 200px;
    @extend %btnSecondary;
    margin: 0 auto;
  }
}
