@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.home {
  background-color: $white;

  &__box {
    display: flex;
    min-height: 100vh;
  }
  &__header {
    color: $textBlack;
    margin-bottom: 32px;
  }

  &__title {
    font-family: Poppins;
    font-size: 48px;
    font-weight: 600;
    line-height: 72px;
    letter-spacing: -1px;
  }

  &__suptitle {
    font-family: Poppins;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }

  &__menuLogo {
    @extend %alignedRow;
    font-size: 32px;
    line-height: 40px;
    color: $textBlack;
  }

  &__menu {
    font-family: Inter;
    font-weight: 600;
    font-size: 14px;

    color: #808191;
    line-height: 20px;
    position: relative;
    border-right: 1px solid #e4e4e4;
    width: 257px;
    min-height: 100vh;
    background-color: rgba($color: $white, $alpha: 0.9);
    padding: 48px 20px 38px;
    transition: transform 0.2s ease;

    &Icon {
      position: relative;
      z-index: 2;
      cursor: pointer;
      display: none;
      width: 32px;
      height: 25px;
      border: none;
      flex-direction: column;
      justify-content: space-between;
      background-color: transparent;
      div {
        transition: all 200ms ease;
        height: 4px;
        border-radius: 4px;
        width: 100%;
        background-color: $textBlack;
      }
      &_active {
        div:nth-child(1) {
          transform: translate(0, 10.5px) rotate(45deg);
        }
        div:nth-child(2) {
          opacity: 0;
        }
        div:nth-child(3) {
          transform: translate(0, -10.5px) rotate(-45deg);
        }
      }
    }

    ul {
      margin-top: 48px;
      height: 75%;

      li:last-of-type {
        position: relative;
        margin-top: auto;
        svg {
          path,
          circle {
            stroke: #808191;
            fill: none;
          }
        }

        &:hover {
          svg {
            path,
            circle {
              stroke: $btnHover;
              fill: none;
            }
          }
        }
      }
      li:last-of-type .active {
        svg {
          path {
            fill: $white;
            stroke: $white;
          }
        }
      }
    }

    li a,
    li span,
    &Logout span {
      margin-bottom: 8px;
      cursor: pointer;
      display: flex;
      align-items: center;
      height: 56px;
      width: 100%;
      padding: 0 20px;
      font-size: 16px;
      line-height: 20px;
      border-radius: 12px;
      color: #808191;
      font-size: 14px;
      transition: all 0.2s ease;

      svg {
        margin-right: 16px;
        max-width: 24px;
        max-height: 24px;
        path {
          transition: all 0.2s ease;
          fill: #808191;
        }
      }

      &:hover {
        color: $btnHover;
        outline: 1px solid $btnHover !important;
        svg {
          path {
            fill: $btnHover;
          }
        }
      }

      &.active {
        color: $white;
        background-color: $primary;
        svg {
          path {
            fill: $white;
            stroke: $white;
          }
          circle {
            stroke: $primary;
          }
        }
      }
    }

    &Footer {
      margin-top: auto;
    }

    &Info {
      display: flex;
      flex-direction: column;
      padding-left: 20px;
    }

    &Username {
      color: $textBlack;
    }

    &Userrole {
      font-size: 13px;
      line-height: 18px;
    }

    &Loading {
      height: 56px;
      width: fit-content;
      margin: 0 auto 8px;
    }
  }

  &__right {
    width: calc(100% - 257px);
    padding: 48px 64px;
  }
}

@media (max-width: 1050px) {
  .home {
    &__right {
      width: 100%;
    }
    &__header {
      display: flex;
      align-items: center;
    }
    &__menu {
      padding-top: 70px;
      position: fixed;
      z-index: 1;
      left: 0;
      transform: translateX(-255px);

      &_active {
        transform: translateX(0);
      }
      &Icon {
        margin-right: 16px;
        display: flex;
      }
    }
  }
}
