@import "../../../scss/colors.scss";
@import "../../../scss/repeat.scss";

.adminsList {
  width: 100%;
  max-height: 92%;
  @extend %scrollList;
  padding: 0 32px;

  overflow-x: hidden;

  ul {
    height: calc(100% - 72px);
    max-height: calc(100% - 72px);
    margin-bottom: 16px;
  }

  &__item {
    @extend %body1;
    font-size: 14px;
    color: #808191;
    margin-top: 24px;
    @extend %alignedRow;
    justify-content: space-between;
    padding-bottom: 24px;
    border-bottom: 1px solid #e4e4e4;

    p {
      word-wrap: break-word;
      max-width: 80%;
    }

    [class~="button"] {
      @extend %btnSecondary, %smallBtn;
      background-color: $textBlack;
      width: 80px;
    }
  }

  &__item > &__loader {
    height: 36px;
    margin: 0 !important;
  }

  &__loader {
    height: 56px;
    width: fit-content;
    margin: 0 auto;
  }

  & > [class~="button"] {
    width: 200px;
    @extend %btnSecondary;
    margin: 0 auto;
  }
}
