@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.usersCard {
  @extend %body2, %alignedRow;
  color: $textBlack;
  width: 100%;
  justify-content: space-between;
  padding-left: 32px;
  position: relative;
  width: 100%;
  min-height: 87px;

  div {
    word-wrap: break-word;
  }

  div:nth-child(1) {
    width: 14.5%;
  }

  div:nth-child(2) {
    width: 17%;
  }

  div:nth-child(3) {
    width: 10.5%;
  }

  div:nth-child(4) {
    width: 24.3%;
  }

  div:nth-child(5) {
    width: 5.8%;
  }

  div:nth-child(6) {
    width: 8.4%;
    text-align: center;
    text-decoration: underline;
    color: #ff754c;
    cursor: pointer;
  }

  div:nth-child(7) {
    width: 48px;

    button {
      @extend %button;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: $white;
      box-shadow: 0px 24px 43px -12px rgba(34, 60, 80, 0.4);
      margin-left: auto;

      &:hover {
        svg path {
          transition: all 0.2s ease;
          fill: $error;
        }
      }
    }
  }
}
