@import "../../scss/repeat.scss";
@import "../../scss/colors.scss";

.PROstatusModal {
  [class~="modal__content"] {
    flex-direction: column;
    width: 345px;
    min-height: 228px;
    box-shadow: 0px 8px 20px rgba(118, 118, 118, 0.25);
    border-radius: 16px;
    padding: 32px 16px;
  }

  h2 {
    @extend %heading6;
  }

  p {
    @extend %body1;
    font-weight: 400 !important;
    text-align: left;
    margin: 16px 0 24px;
    color: #697079;
  }

  div {
    display: flex;
    width: 100%;

    [class~="button"]:nth-child(1) {
      @extend %smallBtnSecondary;
      width: 150px;
      height: 40px;
      margin-right: 4px;
    }

    [class~="button"]:nth-child(2) {
      @extend %smallBtnPrimary;
      width: 150px;
      height: 40px;
    }
  }
}
