@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.admins {
  width: 100%;
  padding: 32px 0;
  height: 84%;
  max-height: 75vh;
  max-width: 554px;
  background-color: $white;
  border-radius: 24px;
  box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

  &__nav {
    padding: 0 32px;
    @extend %alignedRow;
    margin-bottom: 12px;
    &Item {
      @extend %smallBtnSecondary;
      &:last-child {
        margin-left: 12px;
      }
      &[class~="active"] {
        @extend %smallBtnPrimary;
      }
    }
  }
}
