@import "../../scss/colors.scss";

.signIn {
  background-image: url("../../assets/mainBg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  padding: 0 16px 0 16px;

  width: 100%;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  &__inner {
    width: 100%;
    max-width: 516px;
    max-height: 695px;
    border-radius: 20px;
    padding: 116px 100px;

    background: $white;
  }

  &__title {
    font-family: Poppins;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    text-align: center;
    color: $textBlack;
    margin-bottom: 40px;
  }

  &__loader {
    height: 56px;
    width: fit-content;
    margin: 40px auto 0 auto;
  }

  [class~="input"]:nth-of-type(1) {
    margin-bottom: 20px;
  }

  [class~="button"] {
    margin: 40px auto 0 auto;
  }
}
