@import "./colors.scss";

%button {
  transition: background-color 0.2s ease, color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;

  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  height: 36px;
  width: 131px;
}

%center {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

%alignedRow {
  display: flex;
  align-items: center;
}

%checkbox {
  cursor: pointer;
  input {
    display: none;
  }

  div {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s ease;
    margin-right: 12px;
    width: 20px;
    height: 20px;
    border: 2px solid #808191 !important;
    border-radius: 4px;

    svg path {
      transition: all 0.2s ease;
      fill: white;
    }
  }

  input:checked ~ div {
    border: none !important;
    background-color: $primary;
  }
}

%smallBtn {
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;

  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  height: 36px;
  width: 131px;
}

%smallBtnPrimary {
  @extend %smallBtn;
  background-color: $primary;
  color: $white;
  border: none;

  &:hover {
    background-color: $btnHover;
    color: $white;
  }

  &:active {
    background-color: $btnActive;
    color: $white;
  }
}

%smallBtnSecondary {
  @extend %smallBtn;
  background-color: transparent;
  border: 2px solid $textBlack;
  color: $textBlack;

  &:hover {
    color: $btnHover;
    border-color: $btnHover;
  }

  &:active {
    color: $btnActive;
    border-color: $btnActive;
  }
}

%btnSecondary {
  border-radius: 16px;
  background-color: $textBlack;

  &:hover {
    background-color: rgba($textBlack, 0.9);
  }

  &:active {
    background-color: rgba($textBlack, 0.8);
  }
}

%scrollList {
  overflow-y: overlay;
  &::-webkit-scrollbar {
    width: 10px;
    position: relative;
    z-index: 100;
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar-button {
    display: none;
  }

  &::-webkit-scrollbar-thumb {
    transition: background-color 4s linear;
    border-radius: 10px;
    background-color: #b9b9b9f8;
    &:active {
      background-color: #000;
    }
  }
}

