@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.statistics {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 84%;
  max-height: 75vh;

  h3 {
    @extend %subHead1;
  }

  &__top {
    @extend %alignedRow;
    width: 100%;
  }

  &__total {
    width: 47.3%;
    max-width: 499px;
    padding: 32px;
    background-color: $white;
    border-radius: 24px;
    box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

    h3 {
      margin-bottom: 32px;
    }

    & > div {
      display: inline-block;
      padding: 24px;
      width: calc(100% / 3);
      border: 1px solid #e4e4e4;

      &:nth-child(2) {
        border-radius: 16px 0px 0px 16px;
      }

      &:nth-child(3) {
        border-left: none;
        border-right: none;
      }
      &:nth-child(4) {
        border-radius: 0px 16px 16px 0px;
      }

      p:first-child {
        @extend %subHead4;
        color: #808191;
      }

      p:last-child {
        @extend %heading3;

        &::after {
          content: "";
          margin-top: 16px;
          display: block;
          width: 100%;
          height: 2px;
          background-color: $primary;
          border-radius: 1px;
        }
      }
    }
  }

  &__profit {
    @extend %heading1;
    margin: 0 auto;
    color: $primary;
    width: fit-content;
    height: fit-content;
    padding: 32px;
    background-color: $white;
    border-radius: 24px;
    box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

    h3 {
      text-align: center;
      color: $textBlack;
      margin-bottom: 12px;
    }
  }

  &__userStatistics {
    width: 100%;
    height: 404px;
    padding: 32px;
    background-color: $white;
    border-radius: 24px;
    box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

    h3 {
      margin-bottom: 24px;
    }
  }

  &__diagram {
    @extend %subHead3;
  }
}
