@import "../../scss/colors.scss";
@import "../../scss/repeat.scss";

.categories {
  width: 100%;
  padding: 32px 0;
  height: 84%;
  max-height: 75vh;
  max-width: 554px;
  background-color: $white;
  border-radius: 24px;
  box-shadow: 0px 6px 35px -3px rgba(34, 60, 80, 0.2);

  &__nav {
    padding: 0 32px;
    @extend %alignedRow;
    margin-bottom: 12px;

    button {
      @extend %button;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: $white;
      box-shadow: 0px 24px 43px -12px rgba(34, 60, 80, 0.4);
      margin-left: auto;

      &:hover {
        svg path {
          transition: all 0.2s ease;
          fill: $error;
        }
      }
    }

    &Item {
      @extend %smallBtnSecondary;

      &:first-child {
        margin-right: 12px;
      }

      &[class~="active"] {
        @extend %smallBtnPrimary;
      }
    }
  }

  &__loader {
    height: 48px;
    @extend %alignedRow;
    justify-content: center;
    width: fit-content;
    margin-left: auto;
  }
}
